.booking-section {
  background-color: rgba(217, 217, 217, 1);
  border-radius: 16px;
  color: rgba(78, 54, 41, 1);
  font-family: Arial, sans-serif;
  margin-top: 80px;
  margin-bottom: 80px;
  box-sizing: border-box;
  display: flex;
  justify-content: center;
  @media screen and (max-width: 1380px) {
    width: 90%;
    margin: 80px auto;
  }
  @media screen and (max-width: 685px) {
    margin: 60px 16px;
    width: calc(100% - 32px);
  }

  .booking-container {
    width: 1320px;
    display: flex;
    gap: 24px;
    align-items: stretch; /* важно! */
    @media (max-width: 1380px) {
      flex-direction: column;
      gap: 0;
    }
  }

  /* Оба блока должны быть флексами, чтобы выравнивались по высоте */
  .booking-left,
  .booking-right {
    flex: 1;
    display: flex;
    padding: 24px;
    flex-direction: column;
    @media screen and (max-width: 685px) {
      padding: 16px;
    }
  }

  /* Карта занимает всю высоту правого блока */
  .booking-right iframe {
    flex: 1;
    width: 100%;
    height: 100%;
    border: 0;
    border-radius: 16px;
  }

  .booking-span {
    font-size: 15px;
    padding: 8px 30px;
    border-radius: 20px;
    background-color: white;
    width: max-content;
    margin-bottom: 16px;
  }

  .booking-title {
    font-size: 64px;
    margin-bottom: 20px;
    font-weight: 500;
    line-height: 100%;
    @media screen and (max-width: 685px) {
      font-size: 36px;
    }
    & > span {
      font-family: "Arapey", serif;
      font-style: italic;
      font-weight: 400;
      @media screen and (max-width: 685px) {
        font-size: 42px;
      }
    }
  }

  form {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }

  .row {
    display: flex;
    gap: 16px;
    flex-wrap: wrap;
  }

  .input-group {
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .input-group label {
    margin-bottom: 6px;
    font-weight: 500;
  }

  .input-group input,
  .input-group select {
    padding: 12px 16px;
    border-radius: 46px;
    border: none;
    background: white;
    font-size: 14px;
    color: rgba(78, 54, 41, 1);
  }

  .textarea-group {
    position: relative;
    display: flex;
    flex-direction: column;
  }

  .textarea-group textarea {
    padding: 12px 16px;
    border-radius: 16px;
    border: none;
    background: white;
    resize: none;
    min-height: 120px;
    font-size: 14px;
    color: rgba(78, 54, 41, 1);
  }

  .char-count {
    align-self: flex-start;
    font-size: 14px;
    margin-top: 4px;
    padding-left: 16px;
    opacity: 0.7;
  }

  .error {
    color: red;
    font-size: 12px;
    margin-top: 4px;
  }

  .send-btn {
    width: 100%;
    background: rgba(78, 54, 41, 1);
    color: white;
    border: none;
    border-radius: 20px;
    padding: 14px;
    font-size: 16px;
    cursor: pointer;
    transition: 0.3s;
    margin-bottom: 80px;
  }

  .send-btn:hover {
    opacity: 0.9;
  }

  .info-row {
    display: flex;
    justify-content: space-between;
    margin-top: 24px;
    flex-wrap: wrap;
    gap: 12px;
    align-items: flex-end;
  }

  .info-block {
    display: flex;
    flex-direction: column;
    & > span {
      font-size: 15px;
      font-weight: 400;
    }
    & > p {
      font-size: 15px;
      font-weight: 400;
    }
  }

  .booking-right {
    border-radius: 16px;
    overflow: hidden;
  }

  /* Адаптив */
  @media (max-width: 768px) {
    .booking-container {
      flex-direction: column;
    }

    .row {
      flex-direction: column;
    }

    .send-btn {
      width: 100%;
    }
  }
}


@media screen and (max-width: 1320px) {
  .booking-right {
    display: none!important;
  }
}